body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  color: white;
  text-align: center;
  background-color: black;
}
#container {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

#happy-container {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  
  z-index: 100;
}
h1 {
    color: white;
    text-align: center;
    font-family: "Dancing Script", cursive;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  }
  
  h3 {
    font-family: "Josefin Slab", serif;
    color: white;
    text-align: center;
    padding-bottom: 25vh;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  }
  

body {
  background-color: black;
}
.main-heart{
    width: 400px !important;
    height: 400px !important;
    fill: red !important;
    top: 50%;
  left: 50%;
  margin-top: -200px; /* La mitad de la altura */
  margin-left: -200px; /* La mitad del ancho */
    z-index: 2 !important;
    top: 50%;
    left: 50%;
}
#my-heart{
    font-size: 26px;
}
@keyframes flicker {
    0% {
      opacity: 0.8;
    }
    25% {
      opacity: 1;
    }
    50% {
      opacity: 0.9;
    }
    75% {
      opacity: 0.95;
    }
    100% {
      opacity: 0.85;
    }
  }
  
  .fire-effect {
    width: 400px;
    height: 400px;
    background: radial-gradient(circle, rgba(255, 246, 143, 0.9) 10%, rgba(255, 102, 0, 0) 70%);
    border-radius: 50%;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    z-index: -1;
    animation: flicker 2s infinite;
  }
  
  .fire-glow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    z-index: -1;

    height: 100%;
    background: radial-gradient(circle, rgba(255, 204, 51, 0.8) 40%, rgba(255, 204, 51, 0) 70%);
    opacity: 0.7;
  }
  
  .fire-sparks {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;

    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-image: radial-gradient(circle, rgba(255, 230, 204, 0.8) 5%, rgba(255, 204, 51, 0) 60%);
    animation: flicker 2s infinite;
  }
  


#heart {
  position: absolute;
  z-index: -1;
  width: 60px;
  height: 60px;
  background: transparent;
}

#heart path {
    stroke: none;
    -webkit-animation: random 5s infinite;
    animation: random 5s infinite;
  }
@keyframes random {
  15% {
    fill: red;
  }
  30% {
    fill: rgb(255, 63, 185);
  }
  45% {
    fill: rgb(225, 77, 255);
  }
  60% {
    fill: rgb(76, 219, 255);
  }
  75% {
    fill: #fd0c2c;
  }
}
#overlay{
    display: grid;
    place-content: center;
}

#touch{
  font-size: 2em;
}
.main-text{
  font-size: 2.5em;
}